.widget-list-dialog {
  padding: 20px;
  min-width: 400px;

  h2 {
    margin-top: 0;
  }

  .widget-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 15px;
    margin: 20px 0;

    .widget-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 15px;
      border: 1px solid #ddd;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s;

      &:hover {
        background-color: #f5f5f5;
        transform: translateY(-2px);
        box-shadow: 0 2px 8px rgba(0,0,0,0.1);
      }

      mat-icon {
        font-size: 32px;
        width: 32px;
        height: 32px;
        margin-bottom: 8px;
      }

      p {
        margin: 0;
        font-size: 12px;
      }
    }
  }

  .actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
}
